// import moment from "moment";
import axios from "./axiosFactory";
import iziToast from "izitoast/dist/js/iziToast.min.js"; // you have access to iziToast now

// let isoDate = "2021-09-19T05:30:00.000Z";
// let newDate =  moment.utc(isoDate).format('MM/DD/YY');
function sleep(ms) {
  return new window.Promise((resolve) => setTimeout(resolve, ms));
}

async function verifyPayments(callback, token) {
  await axios
    .get(callback, {
      headers: {
        Authorization: `Token ${token}`,
      },
    })
    .then(function (response) {
      // Handle success
      console.log(response);
      iziToast.success({
        title: "PAYMENT VERIFICATION",
        balloon: true,
        position: "topRight",
        animateInside: true,
        message: "You have successfully verified your card",
      });
    })
    .catch(function (error) {
      // Handle error
      console.log(error);
      iziToast.error({
        title: "PAYMENT VERIFICATION ERROR",
        balloon: true,
        position: "topRight",
        animateInside: true,
        message: "The verification process failed",
      });
    });
}

export default function SubscriptionForm() {
  return {
    sub_loading: false,
    valid: false,
    chargeForm: true,
    ticketData: {
      type: "card",
    },

    async closePopup() {
      // Get the query parameters from the popup window URL
      const queryParams = new URLSearchParams(window.location.search);

      // Retrieve the value of the 'param1' parameter
      const id = queryParams.get("p");
      const token = queryParams.get("t");
      const reference = queryParams.get("reference");

      // Log the value of the 'param1' parameter

      var callbackUrl = `/api/subscriptions/${id}/verify/?trxref=${reference}&reference=${reference}`;
      console.log(`${id} - ${token} - ${reference} - ${callbackUrl}`);

      await verifyPayments(callbackUrl, token);
      window.close();
    },

    async createSubscription() {
      this.sub_loading = true;
        const formElement = this.$refs.tiketForm;
        const action = formElement.action;
        const token = formElement.dataset.token;
        let data = new FormData(formElement);

        formElement.querySelectorAll("[name]").forEach((fieldElement) => {
          if(fieldElement.type === 'radio') {
            data.append(fieldElement.name, this.ticketData.type);
          } else {
            data.append(fieldElement.name, fieldElement.value);
          }
        });

        await sleep(3000);

        await axios.post(action, data, {
          headers: {
            "Authorization": `Token ${token}`,
          }
        }).then(async (res) => {
          if(res.data.sub_id && !res.data.message.includes('otp') && !res.data.message.includes('pin')) {
              await iziToast.success({
                title: 'DAILY TICKET SUBSCRIPTION',
                message: res.data.message
              });
              this.sub_loading = false;
              await sleep(3000);
              return window.location.reload();
          }
          const link = `/api/subscriptions/${res.data.data.id}/pay/?ttype=${res.data.ttype}`;
          await axios.get(link, {
            headers: {
              "Authorization": `Token ${token}`,
            }
          }).then(async (resp) => {
            const url = resp.data.authorization_url;
            const popupWindow = window.open(
              url,
              "Paystack Payment",
              "width=600,height=600"
            );

            if (popupWindow) {
              // Popup window opened successfully
              popupWindow.focus();
            } else {
              // Popup window blocked by browser
              alert(
                "Please disable your popup blocker for 'https://dropsride.com/' to proceed with payment."
              );
            }
          }).catch((err) => {
            console.log(err);
          });
          this.sub_loading = false;
          await sleep(3000);
        }).catch(async (e) => {
          await iziToast.error({
            title: 'PAYMENT FAILED',
            mssage: e.response.message
          });
          this.sub_loading = false;
          this.showTicketModal = false;
          await sleep(3000);
        });
    },

    formText() {
      return this.loading ? "Processing..." : "Pay Now";
    },
  };
}
