/* eslint-disable no-unused-vars */
import axios from "./axiosFactory";
import iziToast from "izitoast/dist/js/iziToast.min.js"; // you have access to iziToast now
import htmx from "htmx.org/dist/htmx";
// import Default from '../components/Default';

function sleep(ms) {
  return new window.Promise((resolve) => setTimeout(resolve, ms));
}

function refreshVerifiedStep() {
  htmx.ajax("GET", `/users/get-process/`, {
    target: "#isVerified",
    swap: "outerHTML",
  });
}

export default function Documents() {
  return {
    loading: false,
    edit: false,
    id_proof_of_ownership:
      "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTretNFas6G68WjEFj0z_sx3GG665VjBulQ9cIlXIN8Dw&s",
    vehicle_license_img:
      "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTretNFas6G68WjEFj0z_sx3GG665VjBulQ9cIlXIN8Dw&s",
    autocheck_certificate_img:
      "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTretNFas6G68WjEFj0z_sx3GG665VjBulQ9cIlXIN8Dw&s",
    insurance_img:
      "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTretNFas6G68WjEFj0z_sx3GG665VjBulQ9cIlXIN8Dw&s",

    async pow(file) {
        this.id_proof_of_ownership = file;
    },
    async vl(file) {
        this.vehicle_license_img = file;
    },
    async acc(file) {
        this.autocheck_certificate_img = file;
    },
    async im(file) {
        this.insurance_img = file;
    },

    async documentForm(event) {
      this.loading = true;
      const formElement = this.$refs.docForm;
      const action = formElement.action;
      const token = formElement.dataset.token;
      let data = new FormData(formElement);

      await formElement.querySelectorAll("[name]").forEach((fieldElement) => {
        data.append(fieldElement.name, fieldElement.value);
      });

      console.dir(data);

      await axios
        .patch(action, data, {
          headers: {
            Authorization: `Token ${token}`,
          },
        })
        .then(async function (response) {
          console.log(response);

          refreshVerifiedStep();

          if (response.status === 200 || response.status === 201) {
            await iziToast.success({
              title: "DOCUMENTS ADDED",
              balloon: true,
              position: "topRight",
              animateInside: true,
              message: "You have successfully added documents",
            });
          }

          htmx.ajax("GET", "/users/get-document/", {
            target: "#document",
            swap: "outerHTML",
          });

          await sleep(3000);
        })
        .catch(async function (error) {
          console.log(error);
          if (error.response.data.license) {
            await iziToast.error({
              title: "DOCUMENTS ADDED",
              balloon: true,
              position: "topRight",
              animateInside: true,
              message: error.response.data.license,
            });
            await sleep(3000);
          } else if (error.response.data.license_exp) {
            await iziToast.error({
              title: "DOCUMENTS ADDED",
              balloon: true,
              position: "topRight",
              animateInside: true,
              message: error.response.data.license_exp,
            });
            await sleep(3000);
          } else if (error.response.data.proof_of_ownership) {
            await iziToast.error({
              title: "DOCUMENTS ADDED",
              balloon: true,
              position: "topRight",
              animateInside: true,
              message: error.response.data.proof_of_ownership,
            });
            await sleep(3000);
          } else if (error.response.data.vehicle_license) {
            await iziToast.error({
              title: "DOCUMENTS ADDED",
              balloon: true,
              position: "topRight",
              animateInside: true,
              message: error.response.data.vehicle_license,
            });
            await sleep(3000);
          } else if (error.response.data.autocheck_certificate) {
            await iziToast.error({
              title: "DOCUMENTS ADDED",
              balloon: true,
              position: "topRight",
              animateInside: true,
              message: error.response.data.autocheck_certificate,
            });
            await sleep(3000);
          } else if (error.response.data.insurance) {
            await iziToast.error({
              title: "DOCUMENTS ADDED",
              balloon: true,
              position: "topRight",
              animateInside: true,
              message: error.response.data.insurance,
            });
            await sleep(3000);
          } else if (error.response.data.non_field_errors) {
            await iziToast.error({
              title: "DOCUMENTS ADDED",
              balloon: true,
              position: "topRight",
              animateInside: true,
              message: error.response.data.non_field_errors,
            });
            await sleep(3000);
          } else {
            await iziToast.error({
              title: "DOCUMENTS ADDED",
              balloon: true,
              position: "topRight",
              animateInside: true,
              message: error.message,
            });
            await sleep(3000);
          }
          await sleep(3000);
          return; // window.location.reload();
        });
        this.loading = false;
        this.edit = false;
      return;
    },

    async powChosen(event) {
      this.fileToDataUrl(event, (src) => (this.id_proof_of_ownership = src));
    },

    async vlChosen(event) {
      this.fileToDataUrl(event, (src) => (this.vehicle_license_img = src));
    },

    async accChosen(event) {
      this.fileToDataUrl(
        event,
        (src) => (this.autocheck_certificate_img = src)
      );
    },

    async imChosen(event) {
      this.fileToDataUrl(event, (src) => (this.insurance_img = src));
    },

    fileToDataUrl(event, callback) {
      if (!event.target.files.length) return;

      let file = event.target.files[0],
        reader = new FileReader();

      reader.readAsDataURL(file);
      reader.onload = (e) => callback(e.target.result);
    },

    updateVehicleText() {
        return this.loading ? "Processing..." : "Submit";
    },
  };
}
